<template>
	<view class="">
			<view class="user">
				<image :src="item.userimg"></image>
			</view>
			<view class="right">
				<view class="user-name">
				{{item.username}}
				</view>
				<view class="storey">
					第{{index+1}}楼<span class="time">{{time[index]}}</span>
				</view>
				<rich-text :nodes="item.txt" @click='replyComment(item)'></rich-text>
				<!-- <comment-son :reply='item' :storey='index+1' :fatherid='item.id'></comment-son> -->
				<comment-son :reply='item' :storey='index+1' :fatherid='item.id'></comment-son>
			</view>
			<reply :articleid='articleid' :replyShow="replyShow" :replyType="2" :fatherid="item.id">
				<span class='reply-comment'>
					回复<span class='name'>{{item.username}}</span>:
				</span>
			</reply>
	</view>
</template>

<script>
	import commentSon from '../commentSon/commentSon.vue'
	import reply from '../../reply/reply.vue'
	export default{
		name:'com',
		data(){
			return{
				replyShow:false
			}
		},
		components:{reply,commentSon},
		props:['reply','time','articleid','item'],
		methods:{
			replyComment(item){
				this.fatherid = item.id
				if(!this.$store.state.login){
					uni.navigateTo({
						url:'../../pages/login/login'
					})
				}else{
					this.replyShow = !this.replyShow
					this.$emit('reply-show',this.replyShow)
				}
			}
		}
	}
</script>

<style lang="scss">
	// .comment{
	// 	margin-bottom: 20px;
		.user{
			image{
				height: 40px;
				width: 40px;
				border-radius: 50%;
			}
		}
		.right{
			margin-left: 60px;
			margin-top: -40px;
			.user-name{
				height: 20px;
				line-height: 20px;
			}
			.storey{
				font-size: 14px;
				height: 20px;
				line-height: 20px;         
				color: #999;
				margin-bottom: 15px;
				.time{
					margin-left: 20rpx;
				}
			}
		}
		.reply-comment{
			display: inline-block;
			font-size: 12px;
			color: #999;
			margin: 10px 0;
			.name{
				margin: 0 3px;
			}
		}
	// }
</style>
